import React, { useEffect, useState } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import { Loader, Grid, Post, Image, Video } from "../styles/insta";

const InstagramGrid = () => {
  const [posts, setPosts] = useState([]);
  const [hasMore, setHasMore] = useState(true);
  const [nextPage, setNextPage] = useState(null);

  const accessToken = "YOUR_LONG_LIVED_ACCESS_TOKEN"; // Replace with your token
  const endpoint = `https://graph.facebook.com/v17.0/me/media?fields=id,caption,media_type,media_url,thumbnail_url,permalink,timestamp&access_token=${accessToken}`;

  // Fetch Instagram posts
  const fetchPosts = async (url = endpoint) => {
    try {
      const response = await fetch(url);
      const data = await response.json();

      if (data && data.data) {
        setPosts((prevPosts) => [...prevPosts, ...data.data]);
        setNextPage(data.paging?.next || null);
        setHasMore(!!data.paging?.next);
      }
    } catch (error) {
      console.error("Error fetching Instagram posts:", error);
    }
  };

  useEffect(() => {
    fetchPosts();
  }, []);

  return (
    <InfiniteScroll
      dataLength={posts.length}
      next={() => fetchPosts(nextPage)}
      hasMore={hasMore}
      loader={<Loader>Loading...</Loader>}
    >
      <Grid>
        {posts.map((post) => (
          <Post
            key={post.id}
            href={post.permalink}
            target="_blank"
            rel="noopener noreferrer"
          >
            {post.media_type === "IMAGE" || post.media_type === "CAROUSEL_ALBUM" ? (
              <Image src={post.media_url} alt={post.caption} />
            ) : (
              <Video controls>
                <source src={post.media_url} type="video/mp4" />
         \     </Video>
            )}
          </Post>
        ))}
      </Grid>
    </InfiniteScroll>
  );
};

export default InstagramGrid;
